.listItem:not(:hover) {
  background-color: white !important;
}

.isDragging .listItem:hover {
  background-color: white !important;
}

.listItem:hover .dragHandle {
  visibility: visible;
}

.isDragging .listItem:hover .dragHandle {
  visibility: hidden;
}

.dragHandle {
  visibility: hidden;
}

@media (max-width: 600px) {
  .dragHandle {
    visibility: visible;
  }
}

.dragHandle.isDragging {
  visibility: visible;
}
